import React, { Component } from 'react';

class EditProductButton extends Component{
  constructor(props){
    super(props);
    this.state = {
      editData: this.props.apiData,
      popupVisible: false,
    }
  }



  render(){
    return(
      <div>
        <button>Edit</button>
      </div>
    )
  }
}


export default EditProductButton



